import React, { useState } from "react";
import {ethers} from 'ethers'
import launch from './launch.gif'
export default function Tokengen(){
    let[token,setToken]=useState({
        tName:"",
        symbol:"",
        supply:0
    })
    let [tokenAddress,setTokenAddress] = useState("")
    let contractAddress = "0xfe0B5691Aa003Ff2c24Cc9e071A31664DEDcD64C";
    let abi = [
        "function createToken(string name,string sym, uint totalsupply,uint decimal,address wallet)",
        "function getMyID(address signer)view returns(uint)",
        "function getAddress(uint index) view returns(address)"
    ]
let createToken = async(event)=>{
event.preventDefault();
if(window.ethereum!== undefined){
    console.log("working")
    let provider = new ethers.providers.Web3Provider(window.ethereum);
    let accounts = await provider.send("eth_requestAccounts",[]);
    let signer = provider.getSigner()
    let contract = new ethers.Contract(contractAddress,abi,signer);
   let ct =  await contract.createToken(token.tName,token.symbol,token.supply,7,accounts[0]);
   ct.wait()
}
}

let getAddress = async(event)=>{
event.preventDefault()
    let provider = new ethers.providers.Web3Provider(window.ethereum);
    let accounts = await provider.send("eth_requestAccounts",[]);
     let contract = new ethers.Contract(contractAddress,abi,provider);
     let id = await contract.getMyID(accounts[0]);
     let tokenAddress = await contract.getAddress(id);
     setTokenAddress(tokenAddress)
}

    return(
        <div className="container-md mt-3">
            <div className="alert alert-warning">
                <p>token is set as a decimal 7 defaultly... so add 7 zeroes with your total supply eg: if you make 100 token that will 1000000000(100+000000(seven zeroes))</p>
            </div>
<center>
<div className="card wdth shadow-lg p-3 mb-5 bg-body rounded">
<img src={launch} className="card-img-top" alt="card-head
"/>
    <div className="card-body">
    <form onSubmit={createToken}>
   <div className="mb-3">
   <label className="form-label">enter your token name</label>
    <input type="text" placeholder="@example:Bitcoin" className="form-control" onChange={(event)=>{setToken((prev)=>{return{...prev,tName:event.target.value}})}} required/>
  <p className="form-text">given input is act as a name of your token</p>
   </div>

   <div className="mb-3">
    <label className="form-label">enter your token symbol</label>
    <input type="text" placeholder="@example:BTC" className="form-control" onChange={(event)=>{setToken((prev)=>{return{...prev,symbol:event.target.value}})}} required />
  <p className="form-text">given symbol is act as symbol of your token</p>
   </div>

    <div className="mb-3">
    <label className="form-label">enter your token supply</label>
    <input type="number" placeholder="@example:1000000000000000" className="form-control" onChange={(event)=>{setToken((prev)=>{return{...prev,supply:event.target.value}})}}required/>
    <p className="form-text">given supply act as a token supply of your token</p>
    </div>
    <input type="submit" value="create now!" className="btn shadow-lg btn-primary"/>
   <button onClick={getAddress} className="btn btn-primary ms-1">getAddress</button>
  <p className="mt-1 text-wrap">{tokenAddress}</p>
</form>
    </div>
</div>
</center>
        </div>
    )
}