import { ethers } from 'ethers';
import {useState,useEffect} from 'react';
import './App.css';

function App() {
  let [add,setadd] = useState("")
  let [badge,setBadge] = useState("")
  let [connected,setConnected] = useState(false)
  function badgeshower(){
    let part1 = window.ethereum.selectedAddress.slice("0","5");
    let part2 = window.ethereum.selectedAddress.slice("39","43");
    let badge = part1.concat("...",part2);
    setBadge(badge)
  }
  let connect = async()=>{
 if(window.ethereum !== undefined){
  let provider = new ethers.providers.Web3Provider(window.ethereum);
  let account = await provider.send("eth_requestAccounts",[]);
  setadd(account[0]);
  if(account.length>0){
    localStorage.setItem('isConnected',true)
   badgeshower()
   setConnected(true)
    return {
      accounts:add
    }
  }
 }
else{
  alert("install metamask!");
}
  }


  useEffect(()=>{
setTimeout(()=>{
  if(window.ethereum !== undefined){
  let status = localStorage.getItem("isConnected")
    if(window.ethereum.selectedAddress !== null||status){
  setadd(window.ethereum.selectedAddress)
connect()
    }
  
  }
},2000)
  },[])

  if(window.ethereum !== undefined){
    window.ethereum.on("accountsChanged",()=>{
      window.location.reload();
    })
    window.ethereum.on("chainChanged",()=>{
      window.location.reload();
     // console.log("this is work on chain changed")
    })
  }
  return (


<nav className='navbar navbar-expand-lg navbar-light navcol'>
<div className='container-fluid'>
<a className='navbar-brand' href="/">ZethTO</a>
<div>
<div className='d-flex'>
  <p className='nav-item me-2 mt-3 badge bge bg-secondary'>{badge}</p>
{connected ?<button className='btn btn-primary m-1'>connected</button>: <button className='btn btn-primary m-1' onClick={connect}>connect</button>}
</div>
</div>
</div>
</nav>


  );
}

export default App;
